import './App.css';
import { ReactFlow, MiniMap, Background } from '@xyflow/react';
import '@xyflow/react/dist/style.css';
import FlipRectangleNode from './components/nodes/FlipRectangleNode';
import { useFlowState } from './hook/useFlowState';
import CustomControls from './components/controls/CustomControls';

export const nodeTypes = {
	FlipRectangleNode: FlipRectangleNode
};

function App() {
	const { nodes, edges, onNodeClick, onImport, onSave } = useFlowState();

	return (
		<div className="w-screen h-screen relative">
			<ReactFlow
				nodes={nodes}
				edges={edges}
				fitView={true}
				fitViewOptions={{ maxZoom: 1 }}
				nodeTypes={nodeTypes}
				onNodeClick={onNodeClick}
			>
				<CustomControls onImport={onImport} onSave={onSave} />
				<Background style={{ backgroundColor: '#1e1e1e' }} gap={9999} />
			</ReactFlow>
		</div>
	);
}

export default App;
