import { Handle, Position } from '@xyflow/react';

interface NodeData {
	label: string;
	frontColor: string;
	backColor: string;
	isFlipped?: boolean;
}

const borderRadius = 12;

const FlipRectangleNode = ({ data }: { data: NodeData }) => {
	const isFlipped = data.isFlipped || false;

	return (
		<div style={{ perspective: '1000px', borderRadius: borderRadius }} className="w-auto min-w-96 max-w-128 h-24">
			<div
				style={{
					width: '100%',
					height: '100%',
					position: 'relative',
					transformStyle: 'preserve-3d',
					transform: isFlipped ? 'rotateX(180deg)' : 'rotateX(0deg)',
					transition: 'transform 0.6s',
					borderRadius: borderRadius,
					color: '#1e1e1e'
				}}
			>
				<div
					className="flex items-center justify-center"
					style={{
						position: 'absolute',
						width: '100%',
						height: '100%',
						transform: isFlipped ? 'rotateX(180deg)' : 'rotateX(0deg)',
						backgroundColor: isFlipped ? data.backColor : data.frontColor,
						borderRadius: borderRadius,
						color: '#1e1e1e'
					}}
				>
					{data.label}
				</div>
			</div>

			<Handle
				id={Position.Bottom}
				type="source"
				position={Position.Bottom}
				style={{
					left: '50%',
					bottom: 0,
					transform: 'translateX(-50%)',
					backgroundColor: 'transparent',
					borderColor: 'transparent'
				}}
			/>
			<Handle
				id={Position.Top}
				type="target"
				position={Position.Top}
				style={{
					left: '50%',
					top: 0,
					transform: 'translateX(-50%)',
					backgroundColor: 'transparent',
					borderColor: 'transparent'
				}}
			/>
			<Handle
				id={Position.Left}
				type="target"
				position={Position.Left}
				style={{
					top: '50%',
					left: 0,
					transform: 'translateY(-50%)',
					backgroundColor: 'transparent',
					borderColor: 'transparent'
				}}
			/>
			<Handle
				id={Position.Right}
				type="source"
				position={Position.Right}
				style={{
					top: '50%',
					right: 0,
					transform: 'translateY(-50%)',
					backgroundColor: 'transparent',
					borderColor: 'transparent'
				}}
			/>
		</div>
	);
};

export default FlipRectangleNode;
